import React from 'react';
import { Box, Image } from 'rebass';
import './pendant.css';

class Pendant extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            display: false
        }
        this.changeDisplay = this.changeDisplay.bind(this);
    }

    changeDisplay() {
        if (window.scrollY > 0) {
            document.getElementById('pendant').style.transform = "translateY(0)";
        } else {
            document.getElementById('pendant').style.transform = "translateY(-250px)";
        }
    }

    componentDidMount() {
        window.addEventListener('scroll', this.changeDisplay)
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.changeDisplay)
    }

    render() {
        return (
            <Box width={150} className='pendant' id='pendant'>
                <Image src='/static/mcm10/Mcm10Logo.svg' />
            </Box>
        )
    }
}

export default Pendant